           shop_name:'', 
           update_time:'2019-02-14 10:00'
        }, 
       {
           id:2,
           status:'',
           category:' - ',
           shop_name:'', 
           update_time:'2019-02-13 10:10' 
       },
       {
           id:3,
           status:'',
           category:' - ',
           shop_name:'', 
           update_time:'2019-02-12 10:10' 
       },
   ];

import  MyGroupTd from '@/components/pages/MyGroupTd'

    export default {
        name: 'MyGroupList',        
        components:{
            MyGroupTd
        },
        data(){
            return{
                myOpenGroup
            }
        }
    }
</script>





