<template>
    <div class="container-fluid bg-light">

        <div class="container p-4">
             <h3 class="text-primary"> 
                 <i class="material-icons left">calendar_today</i>
                 
            </h3>
            <vue-event-calendar :events="foodEvents" @monthChanged="" @dayChanged="">
                <template slot-scope="props">
                    <div v-for="(event, index) in props.showEvents" :key="event.id" class="event-item">
                    <!-- event -->
                        <div class="wrapper">
                             <h3 class="title">
                                 {{index+1}} . <router-link to="/shopOrder/shop-1/001/status/">{{event.title}}</router-link>
                                 <small class="badge badge-info" 
                                 :class="{'badge-info':event.status == '','badge-dark':event.status == '','badge-danger':event.status == ''}">
                                     {{event.status}}
                                 </small>
                            </h3>
                             <p class="time">{{event.date}}</p>
                             <p class="desc">{{event.desc}}</p>
                        </div>
                    </div>
                </template>


            </vue-event-calendar>
        </div>




    </div>
</template>

<style lang="scss" scoped>
    .container-fluid{
        min-height:65vh;
    }
</style>



<script>

var foodEvents =  [
    {   
        id:20190214001,
        date: '2019/02/14', // 
        title: '', // 
        desc: 'Miya',
        status:''
    }, 
    {
        id:20190214002,
        date: '2019/02/14', // 
        title: '', // 
        desc: 'Miya',
        status:''
    }, 

    {
        id:20190215001,
        date: '2019/02/15',
        title: '65',
        desc: 'Eric',
        status:'',
        customClass: 'disabled highlight' // Class
    }
    ]

    export default {
        name: 'FoodCalendar',
        props:["category"],
        components:{
      
        },
        data(){
            return{
                foodEvents
            }
        }

    }

</script>





