<template>
    <div class="header-block card border-primary m-2">
        <div class="card-header">
             (only seen by head)
        </div>
        <div class="card-body">
                <div class="btn-group" role="group" aria-label="Basic example">
                    <button type="button" class="btn btn-info" @click.stop="groupStatus"></button>
                    <button type="button" class="btn btn-info" @click.stop="closeGroup"></button>
                    <button type="button" class="btn btn-danger" @click.stop="deleteGroup"></button>
                </div>

        </div>
        
    </div>
</template>

<script>


    export default {
        name: 'HeadBlock',
        methods:{
            groupStatus(){
                var vs = this;
                vs.$router.push({
                    path:vs.$route.path+'/status/'
                })
            },
            closeGroup(){
                var vs = this;
                var sweetWord = {
                    title:'',
                    text:'',
                    type:'warning',
                    showCancelButton: true,
                    cancelButtonText: '',
                    confirmButtonText: '',
                    reverseButtons: true
                };
                vs.$swal(sweetWord).then((result)=>{
                    if(result.value){
                            vs.$router.push({
                                path:vs.$route.path+'/close/'
                            })
                    }else{
                        // console.log('');
                    }
                })
            },
            deleteGroup(){
                var vs = this;
                var sweetWord = {
                    title:'',
                    text:'',
                    type:'warning',
                    showCancelButton: true,
                    cancelButtonText: '',
                    confirmButtonText: '',
                    reverseButtons: true
                };
                vs.$swal(sweetWord).then((result)=>{
                    if(result.value){
                        console.log('');
                    }else{
                        console.log('');
                    }
                })
            
            }
        }
    }

</script>





